    <span class="token keyword">override</span> <span class="token keyword">fun</span> <span class="token function">onViewCreated</span><span class="token punctuation">(</span>view<span class="token operator">:</span> View<span class="token punctuation">,</span> savedInstanceState<span class="token operator">:</span> Bundle<span class="token operator">?</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
        <span class="token keyword">super</span><span class="token punctuation">.</span><span class="token function">onViewCreated</span><span class="token punctuation">(</span>view<span class="token punctuation">,</span> savedInstanceState<span class="token punctuation">)</span>

        counter <span class="token operator">=</span> <span class="token keyword">when</span> <span class="token punctuation">(</span>savedInstanceState<span class="token punctuation">)</span> <span class="token punctuation">{</span>
            <span class="token keyword">null</span> <span class="token operator">-></span>
                arguments<span class="token operator">!!</span><span class="token punctuation">.</span><span class="token function">getInt</span><span class="token punctuation">(</span>ARG_START_VALUE<span class="token punctuation">)</span>
            <span class="token keyword">else</span> <span class="token operator">-></span>
                savedInstanceState<span class="token punctuation">.</span><span class="token function">getInt</span><span class="token punctuation">(</span>KEY_COUNTER<span class="token punctuation">)</span>
        <span class="token punctuation">}</span>

        <span class="token keyword">val</span> textView<span class="token operator">:</span> TextView <span class="token operator">=</span> view<span class="token punctuation">.</span><span class="token function">findViewById</span><span class="token punctuation">(</span>R<span class="token punctuation">.</span>id<span class="token punctuation">.</span>textView<span class="token punctuation">)</span>
        <span class="token keyword">val</span> resetButton <span class="token operator">:</span> Button <span class="token operator">=</span> view<span class="token punctuation">.</span><span class="token function">findViewById</span><span class="token punctuation">(</span>R<span class="token punctuation">.</span>id<span class="token punctuation">.</span>resetButton<span class="token punctuation">)</span>
        <span class="token keyword">val</span> decrementButton <span class="token operator">:</span> Button <span class="token operator">=</span> view<span class="token punctuation">.</span><span class="token function">findViewById</span><span class="token punctuation">(</span>R<span class="token punctuation">.</span>id<span class="token punctuation">.</span>decrementButton<span class="token punctuation">)</span>

        textView<span class="token punctuation">.</span>text <span class="token operator">=</span> <span class="token string">"<span class="token interpolation variable">$counter</span>"</span>

        decrementButton<span class="token punctuation">.</span><span class="token function">setOnClickListener</span> <span class="token punctuation">{</span>
            counter <span class="token operator">-=</span> <span class="token number">1</span>
