        resetButton<span class="token punctuation">.</span><span class="token function">setOnClickListener</span> <span class="token punctuation">{</span>
            counter <span class="token operator">=</span> arguments<span class="token operator">!!</span><span class="token punctuation">.</span><span class="token function">getInt</span><span class="token punctuation">(</span>ARG_START_VALUE<span class="token punctuation">)</span>
